import React from "react";
import Particles, { Main } from "react-tsparticles";
import { loadSeaAnemonePreset } from "tsparticles-preset-sea-anemone";

const ParticleComponent = () => {
  const options = {
    particles: {
      // Konfigurasi umum partikel
      number: {
        value: 100,
      },
      color: {
        value: "#00f",
      },
      shape: {
        type: "circle",
      },
      // ... tambahkan lebih banyak konfigurasi sesuai keinginan
    },
  };

  const initialize = (instance: Main) => {
    // Muat preset "seaAnemone"
    loadSeaAnemonePreset(instance);
  };

  return (
    <Particles
      id="tsparticles"
      options={options}
      init={initialize}
    />
  );
};

export default ParticleComponent;
